
































/*
 * Sidebar
 */
.sidebar[data-v-19dea195] {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 48px 0 0; /* Height of navbar */
  -webkit-box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
          box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}
@media (max-width: 767.98px) {
.sidebar[data-v-19dea195] {
    top: 5rem;
}
}
.sidebar-sticky[data-v-19dea195] {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}
@supports (position: sticky) {
.sidebar-sticky[data-v-19dea195] {
    position: sticky;
}
}
.sidebar .nav-link[data-v-19dea195] {
  font-weight: 500;
  color: #333;
}
.sidebar .nav-link .feather[data-v-19dea195] {
  margin-right: 4px;
  color: #999;
}
.sidebar .nav-link.active[data-v-19dea195] {
  color: #007bff;
}
.sidebar .nav-link:hover .feather[data-v-19dea195],
.sidebar .nav-link.active .feather[data-v-19dea195] {
  color: inherit;
}
.sidebar-heading[data-v-19dea195] {
  font-size: .75rem;
  text-transform: uppercase;
}

/*
 * Navbar
 */
.navbar-brand[data-v-19dea195] {
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, .25);
  -webkit-box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
          box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
}
.navbar .navbar-toggler[data-v-19dea195] {
  top: .25rem;
  right: 1rem;
}
.navbar .form-control[data-v-19dea195] {
  padding: .75rem 1rem;
  border-width: 0;
  border-radius: 0;
}
.form-control-dark[data-v-19dea195] {
  color: #fff;
  background-color: rgba(255, 255, 255, .1);
  border-color: rgba(255, 255, 255, .1);
}
.form-control-dark[data-v-19dea195]:focus {
  border-color: transparent;
  -webkit-box-shadow: 0 0 0 3px rgba(255, 255, 255, .25);
          box-shadow: 0 0 0 3px rgba(255, 255, 255, .25);
}




